// netlify/functions/server.js
// This function forwards requests to the Nuxt SSR server

export const handler = async function(event, context) {
  try {
    // Import the Nuxt server from the functions-internal directory
    // Note: in production, Netlify will use the actual server code generated by Nuxt
    const { handler } = await import('../../.netlify/functions-internal/server/server.mjs');
    
    // Forward the request to the Nuxt server
    return await handler(event, context);
  } catch (error) {
    console.error('Error in Netlify function:', error);
    
    // Return a custom error page
    return {
      statusCode: 500,
      headers: {
        'Content-Type': 'text/html',
      },
      body: `
        <!DOCTYPE html>
        <html>
          <head>
            <title>Error - Nuxt on Netlify</title>
            <style>
              body { font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif; max-width: 600px; margin: 0 auto; padding: 40px 20px; line-height: 1.5; }
              h1 { color: #dc0000; }
              pre { background: #f5f5f5; padding: 10px; border-radius: 4px; overflow-x: auto; }
            </style>
          </head>
          <body>
            <h1>Server Error</h1>
            <p>The application encountered an error. Please try again later.</p>
            <p>Details: ${error.message}</p>
            <pre>${error.stack || 'No stack trace available'}</pre>
          </body>
        </html>
      `
    };
  }
}; 